export default [
    {
      path: '/',
      name: 'index',
      component: import('@/views/Index'),
      redirect: '/fault'
    },
    {
      path: '/test',
      component: () => import('@/views/fault/test'),
    },
    {
      path: '/fault',
      name: 'fault',
      component: () => import('@/views/fault/Fault'),
      children: [
        {
          path: '/',
          redirect: 'orderDealer'
        },
        {
          path: 'orderDealer',
          name: 'orderDealer',
          component: () => import('@/views/fault/OrderDealer'),
          meta: {
            title: '车辆异常'
          }
        },
        {
          path: 'filterDealer',
          name: 'filterDealer',
          component: () => import('@/views/fault/FilterDealer'),
          meta: {
            title: '筛选经销商'
          }
        },
        {
          path: 'feedback',
          name: 'feedback',
          component: () => import('@/views/fault/Feedback'),
          meta: {
            title: '预约成功'
          }
        }]
    }]


