const newLocal = false;
// 开关:是否是为了给普通经销商做演示
export const IS_DEMONSTRATION_FOR_COMMON_USER = newLocal;

// 是否强制所有请求访问本地 MOCK
export const AJAX_LOCALLY_ENABLE = false;
// 是否开启监控
export const MONITOR_ENABLE = true;
// 路由默认配置
export const ROUTER_DEFAULT_CONFIG = {
	waitForData: true,
	transitionOnLoad: true
};

export const PROD_SERVER_PREFIX = "http://58.87.74.241:10001";
export const TEST_SERVER_PREFIX = "http://192.168.146.9:10001";
export const DEV_SERVER_PREFIX = "http://192.168.146.9:10004";
// axios 默认配置
export const AXIOS_DEFAULT_CONFIG = {
	timeout: 20000,
	baseURL: process.env.NODE_ENV === "development" ? "/api" : TEST_SERVER_PREFIX,
	maxContentLength: 2000,
	headers: {
		channel: " four-dimensional",
		APPKEY: " four-dimensional"
	}
};

// vuex 默认配置
export const VUEX_DEFAULT_CONFIG = {
	strict: process.env.NODE_ENV !== "production"
};

export const CONSOLE_REQUEST_ENABLE = false; // 开启请求参数打印
export const CONSOLE_RESPONSE_ENABLE = false; // 开启响应参数打印
export const CONSOLE_MONITOR_ENABLE = false; // 监控记录打印

export default {
	IS_DEMONSTRATION_FOR_COMMON_USER
};



